
















import Vue from 'vue';
import {Video} from '@/polyphony/youtube';

export default Vue.extend({
  name: 'VideoList',
  props: ['videos'],
  components: {
    Video: () => import ('@/components/Video.vue'),
  },
  data() {
    return {
      vs: [],
    }
  },
  async created() {
    await Promise.all(this.videos.map(
     (video: Video) => video.fetchVideoInfo()
    ));
    this.vs = this.videos;
  },
})
